import * as React from 'react'
import withStyles, { WithStyles, StyleRulesCallback } from 'material-ui/styles/withStyles'
import Typography from 'material-ui/Typography'

interface AnchorDictionary {
  skill: string,
  descriptions: string[],
}

const anchorDictionary: AnchorDictionary[] = [
  {
    skill: 'PHP',
    descriptions: [
      'Symfony2',
    ],
  },
  {
    skill: 'Java/Kotlin',
    descriptions: [
      'Android',
      '',
      'MVVM + Layerd Archtecture',
    ],
  },
  {
    skill: 'React Native',
    descriptions: [
      '',
      '',
      'TypeScriptReduxNativeBasereact-navigationreact-native-fs',
    ],
  },
]

const Anchor = (props: WithStyles) => (
  <div className={props.classes.container}>
    <Typography variant='headline'>
      
    </Typography>

    {
      anchorDictionary.map(it =>
        <React.Fragment>
          <Typography variant='title'>
            {it.skill}
          </Typography>

          <Typography variant='body2'>
            <ul className={props.classes.ul}>
              {
                it.descriptions.map(description =>
                  <li>{description}</li>,
                )
              }
            </ul>
          </Typography>
        </React.Fragment>,
      )
    }
  </div>
)

const styles: StyleRulesCallback = theme => ({
  container: {
  },
  ul: {
    marginTop: '8px',
    marginBottom: '8px',
  },
})

export default withStyles(styles)(Anchor)



