import * as React from 'react'
import withStyles, { WithStyles, StyleRulesCallback } from 'material-ui/styles/withStyles'
import Typography from 'material-ui/Typography'

interface ProfileDictionary {
  term: string,
  discription: string,
}

const profileDictionary: ProfileDictionary[] = [
  { term: '', discription: '  (Yamamoto Shinnosuke)' },
  { term: '', discription: '1993/04/02' },
  { term: '', discription: '//DIY///' },
  { term: 'Strength Finder', discription: '////' },
]

const Profile = (props: WithStyles) => (
  <div className={props.classes.container}>
    <Typography variant='headline' className={props.classes.headline}>
      
    </Typography>

    <Typography variant='body2'>
      {
        profileDictionary.map(it =>
          <dl className={props.classes.dl}>
            <dt className={props.classes.dt}>{it.term}</dt>
            <dd>{it.discription}</dd>
          </dl>,
        )
      }
    </Typography>
  </div>
)

const styles: StyleRulesCallback = theme => ({
  container: {
  },
  headline: {
    marginTop: '0px',
  },
  dl: {
    marginTop: '8px',
    marginBottom: '8px',
  },
  dt: {
    float: 'left',
    width: '120px',
    fontWeight: 'bold',
  },
})

export default withStyles(styles)(Profile)


