import * as React from 'react'
import withStyles, { WithStyles, StyleRulesCallback } from 'material-ui/styles/withStyles'
import Typography from 'material-ui/Typography'

interface OrganizationDescription {
  organization: string,
  date: string,
  works: WorkDescription[],
}

interface WorkDescription {
  duty: string,
  descriptions: string[],
}

const organizationDescription: OrganizationDescription[] = [
  {
    organization: '',
    date: '2018/07 - ',
    works: [
      {
        duty: 'Android',
        descriptions: [
          'Android',
        ],
      },
    ],
  },
  {
    organization: '',
    date: '2019/10 - ',
    works: [
      {
        duty: '',
        descriptions: [
          'React Native',
        ],
      },
    ],
  },
  {
    organization: 'SideWorks',
    date: '2018/09 - 2019/07',
    works: [
      {
        duty: '',
        descriptions: [
          'Expo',
        ],
      },
    ],
  },
  {
    organization: '',
    date: '2016/04 - 2018/04',
    works: [
      {
        duty: '',
        descriptions: [
          'Symfony2EC',
          '',
        ],
      },
      {
        duty: '',
        descriptions: [
          'Android',
          'React Native',
        ],
      },
      {
        duty: 'CS',
        descriptions: [
          '',
        ],
      },
    ],
  },
  {
    organization: '',
    date: '2017/06 - 2017/12',
    works: [
      {
        duty: '',
        descriptions: [
          'Android',
        ],
      },
    ],
  },
]

const Anchor = (props: WithStyles) => (
  <div className={props.classes.container}>
    <Typography variant='headline'>
      
    </Typography>

    {
      organizationDescription.map(it =>
        <React.Fragment>
          <Typography variant='title'>
            {it.organization}
          </Typography>

          <Typography variant='body2'>
            <dt className={props.classes.dt}></dt>
            <dd>{it.date}</dd>

            {
              it.works.map(work =>
                <React.Fragment>
                  <p className={props.classes.p}>{work.duty}</p>
                  <ul className={props.classes.ul}>
                    {
                      work.descriptions.map(description =>
                        <li>{description}</li>,
                      )
                    }
                  </ul>
                </React.Fragment>,
              )
            }
          </Typography>
        </React.Fragment>,
      )
    }
  </div>
)

const styles: StyleRulesCallback = theme => ({
  container: {
  },
  p: {
    marginTop: '8px',
    marginBottom: '8px',
  },
  dt: {
    float: 'left',
    fontWeight: 'bold',
  },
  ul: {
    marginTop: '8px',
    marginBottom: '8px',
  },
})

export default withStyles(styles)(Anchor)



